$verylargephone:1200px;
$largephone:992px;
$mediumphone:768px;
$smallphone:576px;

/*mixinmixinmedia queriesmixin
media querieswidth*/

//
@mixin xl-width() {
    @media (min-width:$verylargephone) {
        @content;
    }
}

//
@mixin lg-width() {
    @media(min-width:$largephone) {
        @content;
    }
}

//
@mixin md-width() {
    @media (min-width: $mediumphone) {
        @content;
    }
}

//
@mixin sm-width() {
    @media (min-width: $smallphone) {
        @content;
    }
}

