@mixin hidetext {
    text-indent: 110%;
    white-space: nowrap;
    overflow: hidden;
}

@mixin flexcenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin size($w, $h) {
    width: $w+px;
    height: $h+px;
}

@mixin fontsize($fz, $lh) {
    font-size: $fz+px;
    line-height: $lh+px;
}

@mixin poa ($top, $left) {
    position: absolute;
    top: $top+px;
    left: $left+px;
}

@mixin center($position) {
    position: absolute;

    @if $position=='vertical' {
        top: 50%;
        transform: translateY(-50%);
    }

    @else if $position=='horizontal' {
        left: 50%;
        transform: translate(-50%);
    }

    @else if $position=='both' {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

@mixin circle($w, $h) {
    width: $w+px;
    height: $h+px;
    border-radius: 50%;
}

@mixin container($w) {
    max-width: $w+px;
    margin: 0 auto;
}